import { showMainContent } from './content';
import { getTemp, getEPALevel, getCountryCode } from './data-functions';

// Info bar
export function populateInfoBar(responseData) {
  if (responseData === null) return;
 
  try {
    const { icon, text } = responseData.current.condition;
    const { name, country } = responseData.location;
    const { usEPAIndex } = getEPALevel(responseData);
    const { temp } = getTemp(responseData);

    const infoBarLeft = document.querySelector('#info-bar .left');
    infoBarLeft.innerHTML = `<img src='https://${icon}' alt='${text}' title='${text}' width='32'><span class='temperature'>${temp}</span><span class='location'>${name}, <span class="country">${country}</span></span>`;

    const infoBarRight = document.querySelector('#info-bar .right');
    infoBarRight.innerHTML = `Air quality: ${usEPAIndex}`;

    getCountryCode();
  } catch(err) {
    console.log('Error:', err.message);
  }
}

// Nav bar
const nav = document.querySelector('nav');
nav.addEventListener('click', async (e) => {
  if (e.target.tagName === 'BUTTON') {
    const button = e.target;

    nav.querySelectorAll('button').forEach(btn => {
      btn.classList.remove('current');
    });

    button.classList.add('current');
    localStorage.setItem('current', button.classList[0]);

    try {
      const latitude = localStorage.getItem('latitude');
      const longitude = localStorage.getItem('longitude');
      const location = `${latitude},${longitude}`;

      const response = await fetch(`https://api.weatherapi.com/v1/forecast.json?key=6f10d6c1c5df40c88d094704240605&q=${location}&days=5&aqi=yes&alerts=yes`, { mode: 'cors' });
      const responseData = await response.json();

      const currentBtn = button.classList[0];
      showMainContent(responseData, currentBtn);
    } catch (err) {
      console.log('Error:', err.message);
      throw err;
    }
  }
});

