/* eslint-disable no-console */
import { populateInfoBar } from './header';
import { showMainContent } from './content';
import { getTemp, getEPALevel, getCloudCoverage } from './data-functions';

export async function searchWeather(location) {
  try {
    const response = await fetch(`https://api.weatherapi.com/v1/forecast.json?key=6f10d6c1c5df40c88d094704240605&q=${location}&days=5&aqi=yes&alerts=yes`, { mode: 'cors' });
    const responseData = await response.json();

    const input = document.querySelector('header form input');

    if (responseData.location) {
      localStorage.setItem('latitude', responseData.location.lat);
      localStorage.setItem('longitude', responseData.location.lon);

      // Set day/night theme
      if (responseData.current.is_day === 0) {
        localStorage.setItem('dayOrNight', 'theme-day');
      } else {
        localStorage.setItem('dayOrNight', 'theme-night');
      }

      const dayOrNight = localStorage.getItem('dayOrNight');
      document.documentElement.className = dayOrNight;

      // Find current page
      const currentBtn = document.querySelector('nav button.current').classList[0];

      getTemp(responseData);
      populateInfoBar(responseData);
      showMainContent(responseData, currentBtn);
      getEPALevel(responseData);
      getCloudCoverage(responseData);

      input.classList.remove('error');
      return responseData;
    }
    
    input.classList.add('error');
    return null;
  } catch (err) {
    console.log('Error:', err.message);
    throw err; 
  }
}

// Search bar
const searchBtn = document.querySelector('header form button');
searchBtn.addEventListener('click', async (e) => {
  e.preventDefault();

  const location = document.querySelector('form input').value.replace(/ /g, '+');

  if (location !== '') {
    searchWeather(location);
  }

  const searchBar = document.querySelector('header form #search');
  searchBar.value = '';
});