import { getTemp, getEPALevel, getCloudCoverage, getCountryCode } from './data-functions';

function showToday(responseData) {
  console.log(responseData);

  // Get data
  const { name, country, localtime  } = responseData.location;
  const [localDate, localTime] = localtime.split(' ');
  const { text, icon } = responseData.current.condition;
  const { last_updated, is_day, wind_mph, wind_kph, wind_degree, wind_dir, humidity, feelslike_c, feelslike_f, uv } = responseData.current;
  const [lastUpdatedDate, lastUpdatedTime] = last_updated.split(' ');
  const { usEPAIndex } = getEPALevel(responseData);
  const { cloud } = getCloudCoverage(responseData);
  const { temp } = getTemp(responseData);
  const { maxtemp_c } = responseData.forecast.forecastday[0].day;
  const { mintemp_c } = responseData.forecast.forecastday[0].day;
  const { sunrise } = responseData.forecast.forecastday[0].astro;
  const { sunset } = responseData.forecast.forecastday[0].astro;

  // Infobox top
  const location = document.createElement('h2');
  const countryName = document.createElement('span');
  countryName.className = 'country';
  countryName.append(`${country}`);
  location.append(`${name}, `, countryName);

  const locationLastUpdated = document.createElement('span');
  locationLastUpdated.className = 'last-updated';
  locationLastUpdated.append(`As of ${lastUpdatedTime}`);

  const top = document.querySelector('.info-box .top');
  top.innerHTML = '';
  top.append(location, locationLastUpdated)

  // Infobox bottom
  const temperature = document.createElement('span');
  temperature.className = 'temperature';
  temperature.append(temp);

  const condition = document.createElement('span');
  condition.className = 'condition';
  condition.append(text);

  const highsAndLows = document.createElement('span');
  highsAndLows.className = 'highs-lows';
  const highs = document.createElement('span');
  highs.className = 'temperature';
  highs.textContent = `${maxtemp_c}°C`;
  const lows = document.createElement('span');
  lows.className = 'temperature';
  lows.textContent = `${mintemp_c}°C`;
  const textNode1 = document.createTextNode('Day: ');
  const textNode2 = document.createTextNode(' • ');
  const textNode3 = document.createTextNode('Night: ');
  highsAndLows.append(textNode1, highs, textNode2, textNode3, lows);

  const tempIcon = document.createElement('img');
  tempIcon.src = `https:${icon}`;
  tempIcon.alt = text;
  tempIcon.classList.add('icon-64', 'icon');

  const bottom = document.querySelector('.info-box .bottom');
  bottom.innerHTML = '';
  bottom.append(temperature, condition, highsAndLows, tempIcon);

  const infoBox = document.querySelector('.info-box');
  infoBox.append(top, bottom);

  getCountryCode();
}

