import countryData from './countries.json';

// Temperature
export function getTemp(responseData) {
  const { temp_c, temp_f } = responseData.current;
  let temp;

  if (localStorage.getItem('temp') === 'celsius') {
    temp = `${temp_c}°C`;
  } else {
    temp = `${temp_f}°F`;
  }

  return { temp };
}

function updateTemp() {
  const temps = document.querySelectorAll('.temperature');
  temps.forEach(temp => {
    const temperatureStr = temp.textContent.slice(0, -2);
    let temperature = parseFloat(temperatureStr);

    if (temp.textContent.includes('°C')) {
      temperature = (temperature * 9/5) + 32;
      temp.textContent = temperature.toFixed(1) + '°F';
    } else if (temp.textContent.includes('°F')) {
      temperature = (temperature - 32) * 5/9;
      temp.textContent = temperature.toFixed(1) + '°C';
    }
  });
}

const tempUnitToggle = document.querySelector('#switch-checkbox');
tempUnitToggle.addEventListener('change', () => {
  if (tempUnitToggle.checked) {
    localStorage.setItem('temp', 'fahrenheit');
    updateTemp();
  } else {
    localStorage.setItem('temp', 'celsius');
    updateTemp();
  }
});

// US EPA Index
export function getEPALevel(responseData) {
  let { 'us-epa-index': usEPAIndex } = responseData.current.air_quality;

  switch (responseData.current.air_quality['us-epa-index']) {
    case 1:
      usEPAIndex = 'Good';
      break;
    case 2:
      usEPAIndex = 'Moderate';
      break;
    case 3:
      usEPAIndex = 'Slightly unhealthy';
      break;
    case 4:
      usEPAIndex = 'Unhealthy';
      break;
    case 5:
      usEPAIndex = 'Very unhealthy';
      break;
    case 6:
      usEPAIndex = 'Hazardous';
      break;
  }

  return { usEPAIndex };
}

// Cloud coverage
export function getCloudCoverage(responseData) {
  let { cloud } = responseData.current;

  switch (true) {
    case cloud >= 0 && cloud <= 10:
      cloud = 'Sunny/Clear';
      break;
    case cloud >= 10 && cloud <= 20:
      cloud = 'Fair';
      break;
    case cloud >= 20 && cloud <= 30:
      cloud = 'Mostly sunny';
      break;
    case cloud >= 30 && cloud <= 60:
      cloud = 'Partly cloudy';
      break;
    case cloud >= 60 && cloud <= 70:
      cloud = 'Partly sunny';
      break;
    case cloud >= 70 && cloud <= 90:
      cloud = 'Mostly cloudy';
      break;
    case cloud >= 90 && cloud <= 100:
      cloud = 'Overcast';
      break;
  }

  return { cloud };
}

// Abbreviate country names
export function getCountryCode() {
  const countryElements = document.querySelectorAll('.country');
  countryElements.forEach(el => {
    const countryName = el.textContent.trim(); 

    if (Object.values(countryData).includes(countryName)) {
      const countryCode = Object.keys(countryData).find(key => countryData[key] === countryName);
      el.textContent = countryCode;
    }
  });
}