/* eslint-disable no-empty-function */
/* eslint-disable arrow-body-style */
/* eslint-disable no-undef */
/* eslint-disable prefer-template */
/* eslint-disable no-unused-vars */
/* eslint-disable no-console */
import './style.css';
import { searchWeather } from './search';
import './data-functions';

// DOM loaded
document.addEventListener('DOMContentLoaded', () => {
  const containerDiv = document.querySelector('#container');
  containerDiv.style.display = 'block';

  // Check for localStorage keys for temperature toggle
  const tempValue = localStorage.getItem('temp');
  const tempToggle = document.querySelector('#switch-checkbox');

  if (tempValue) {
    if (tempValue === 'celsius') {
      tempToggle.checked = false;
    } else {
      tempToggle.checked = true;
    }

    localStorage.setItem('temp', tempValue);
  } else {
    localStorage.setItem('temp', 'celsius');
    tempToggle.checked = false;
  }

  // Check for localStorage keys for day/night
  const dayOrNight = localStorage.getItem('dayOrNight');

  if (dayOrNight) {
    document.documentElement.className = dayOrNight;
  }

  // Check for localStorage keys for current button
  const currentValue = localStorage.getItem('current');

  if (currentValue) {
    const navBtns = document.querySelectorAll('nav button');
    navBtns.forEach(button => {
      button.classList.remove('current');

      if (button.classList.contains(currentValue)) {
        button.classList.add('current');
      }
    });
  } else {
    localStorage.setItem('current', 'today');
  }

  // Check for localStorage keys for Geolocation API
  const userLocationPermission = localStorage.getItem('userLocationPermission');

  if (userLocationPermission === 'denied') {
    searchWeather('Los Angeles');
  } else if (userLocationPermission === 'granted') {
    const latitude = localStorage.getItem('latitude');
    const longitude = localStorage.getItem('longitude');
    const location = `${latitude},${longitude}`;
    searchWeather(location);

    return;
  }

  // If no keys, ask user for location
  if (navigator.geolocation) {
    navigator.geolocation.getCurrentPosition(

      // Success callback function
      (position) => {
        const {latitude} = position.coords;
        const {longitude} = position.coords;
        const location = `${latitude},${longitude}`;

        // Store the user's permission as granted and get location
        localStorage.setItem('userLocationPermission', 'granted');
        localStorage.setItem('latitude', position.coords.latitude);
        localStorage.setItem('longitude', position.coords.longitude);
        
        searchWeather(location);
      },

      // Failure callback function
      (error) => {
        if (error.code === error.PERMISSION_DENIED) {
          // Store the user's permission as denied
          localStorage.setItem('userLocationPermission', 'denied');

          searchWeather('Los Angeles');
        } else {
          console.error('Error getting user location:', error);
        }
      }
    );
  } else {
    console.error('Geolocation is not supported by this browser.');
  }
});